import React from 'react';
import styled from 'styled-components';

import Landing, { Col, Container, Row, Wrapper } from '../landing';
import { resumeContext } from '../index';
import { formatDate } from '../../utils/date';

const Resume = () => {
  const {
    basics: { summary },
    work,
    languages,
    skills,
  } = React.useContext(resumeContext);

  return (
    <>
      <Landing displayResume />
      <Container>
        <Wrapper style={{ width: 800 }}>
          <Section>
            <About>{summary}</About>
          </Section>
          <Section>
            <SectionTitle>Work</SectionTitle>
            {work.map(details => (
              <Work>
                <WorkInfo>
                  <CompanyTitle>
                    {details.website ? (
                      <a href={details.website}>{details.company}</a>
                    ) : (
                      details.company
                    )}
                  </CompanyTitle>
                  <WorkDate>{`${formatDate(
                    details.startDate,
                    'MMMM YYYY',
                  )} - ${formatDate(details.endDate, 'MMMM YYYY')}`}</WorkDate>
                </WorkInfo>
                <Technologies>
                  {details.technologies.map(technology => (
                    <span>{technology}</span>
                  ))}
                </Technologies>
                <Summary>{details.summary}</Summary>
              </Work>
            ))}
          </Section>
          <Section>
            <Row>
              <SectionTitle inline>Languages</SectionTitle>
              {languages.map(lang => (
                <Language>
                  <LanguageTitle>{`${lang.language}: `}</LanguageTitle>
                  <LanguageFluency>{lang.fluency}</LanguageFluency>
                </Language>
              ))}
            </Row>
          </Section>
          <Section>
            <Row>
              <SectionTitle inline>Skills</SectionTitle>
              {skills.map(skill => (
                <Col>
                  <SkillCategory>{skill.name}</SkillCategory>
                  <Skills>
                    {skill.keywords.map(keyword => (
                      <Skill>{keyword}</Skill>
                    ))}
                  </Skills>
                </Col>
              ))}
            </Row>
          </Section>
        </Wrapper>
      </Container>
    </>
  );
};

export const Section = styled.div`
  padding: 0 5px;

  &:last-child {
    margin-bottom: 30px;
  }
`;

export const SectionTitle = styled.h2`
  margin-bottom: 10px;
  position: relative;
  padding-left: 30px;
  ${props => props.inline && 'display: inline-block; width: 200px;'}

  &:before {
    content: '//';
    position: absolute;
    left: 0;
    top: 0;
    color: rgba(0, 0, 0, 0.2);
  }
`;

export const Summary = styled.p`
  margin-top: 0;
`;

export const About = styled.p``;

export const Work = styled.div`
  h3 {
    margin-top: 10px;
  }

  p {
    margin-bottom: 26px;
  }
`;

export const WorkInfo = styled(Row)`
  align-items: center;
  justify-content: space-between;
`;

export const Technologies = styled(Row)`
  margin-bottom: 10px;

  span {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);

    &:not(:last-child) {
      margin-right: 20px;
      position: relative;

      &:after {
        content: '';
        background-color: rgba(0, 0, 0, 0.4);
        width: 1px;
        height: 100%;
        position: absolute;
        top: 0;
        right: -12px;
        transform: rotateZ(25deg);
      }
    }
  }
`;

export const CompanyTitle = styled.h3`
  margin: 20px 0 10px;
  position: relative;

  a:after {
    content: ' (website)';
    position: absolute;
    top: 0;
    left: 100%;
    opacity: 0;
    visibility: hidden;
    user-select: none;
    color: rgba(0, 0, 0, 0.2);
    transition: all 0.2s ease-out;
  }

  &:hover {
    a:after {
      left: calc(100% + 10px);
      opacity: 1;
      visibility: visible;
    }
  }
`;

export const WorkDate = styled.time``;

export const Language = styled(Col)`
  margin-right: 50px;
`;

export const LanguageTitle = styled.h4`
  margin-bottom: 10px;
`;

export const LanguageFluency = styled.span`
  font-weight: normal;
`;

export const SkillCategory = styled(LanguageTitle)``;

export const Skills = styled(Row)`
  flex-wrap: wrap;
  line-height: 2;
`;

export const Skill = styled.span`
  &:not(:last-child) {
    margin-right: 20px;
    position: relative;

    &:after {
      content: '';
      background-color: rgba(0, 0, 0, 0.4);
      width: 1px;
      height: 28px;
      position: absolute;
      top: 2px;
      right: -12px;
      transform: rotateZ(25deg);
    }
  }
`;

export default Resume;
