import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { resumeContext } from '../index';

const Landing = ({ displayResume }) => {
  const {
    basics: { name, label, labelNote, profiles },
  } = React.useContext(resumeContext);

  return (
    <Hero centered={!displayResume}>
      <Card bordered={!displayResume}>
        {displayResume && <BackLink to="/">Back</BackLink>}
        <Intro row={!displayResume}>
          <Name>
            <FirstName>{name.split(' ')[0]}</FirstName>
            <Surname broken={!displayResume}>{name.split(' ')[1]}</Surname>
          </Name>
          <Col>
            <Title>{label}</Title>
            {labelNote && (
              <SmallTitle hidden={displayResume}>{labelNote}</SmallTitle>
            )}
          </Col>
        </Intro>
        <Links row={!displayResume}>
          {(profiles || []).map(profile => (
            <LinkContainer key={Math.random()} inline={!displayResume}>
              <span />
              <a href={profile.url} target="_blank">
                {`${profile.network}${
                  displayResume
                    ? `: ${profile.url.slice(profile.url.lastIndexOf('/') + 1)}`
                    : ''
                }`}
              </a>
            </LinkContainer>
          ))}
          {!displayResume && (
            <LinkContainer>
              <span />
              <Link to="/resume">Resume</Link>
            </LinkContainer>
          )}
        </Links>
      </Card>
    </Hero>
  );
};

export const Container = styled.div`
  width: 100vw;
  display: flex;
  justify-content: center;
  transition: all 0.5s ease-out;
`;

export const Hero = styled(Container)`
  height: ${props => (props.centered ? '100vh' : 'auto')};
  align-items: ${props => (props.centered ? 'center' : 'initial')};
`;

export const Wrapper = styled.div`
  width: 440px;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #fff;
  margin: 0 40px;
`;

export const Card = styled(Wrapper)`
  ${props =>
    props.bordered
      ? 'border: 2px solid #000;'
      : 'border-bottom: 2px solid #000;width: 800px'};
  padding: 5px 0 0;

  &:before {
    content: '';
    position: absolute;
    background-color: #fff;
    top: -7px;
    width: 100%;
    height: 100%;
    ${props =>
      props.bordered
        ? 'right: 3px;border: 2px solid #000;'
        : 'right: 0;border: none;'};
  }
`;

export const Row = styled.div`
  display: flex;
  flex-direction: row;
`;

export const Col = styled.div`
  display: flex;
  flex-direction: column;
`;

export const Intro = styled(Row)`
  justify-content: space-between;
  position: relative;
  flex-direction: column;
  align-items: flex-start;
`;

export const Links = styled(Row)`
  position: relative;
  justify-content: flex-start;
  padding: 10px 10px 10px 7px;
  flex-direction: ${props => (props.row ? 'row' : 'column')};
`;

export const Name = styled.h1`
  font-size: 48px;
  margin: 10px 0 0 5px;
`;

export const FirstName = styled.span`
  display: inline;
  margin-right: 10px;
`;

export const Surname = styled.span`
  margin: 10px 10px 0 0;
  display: inline-block;
`;

export const Title = styled.h4`
  font-size: 32px;
  margin: 0 10px 0 6px;
`;

export const SmallTitle = styled.h6`
  font-size: 16px;
  margin: 0 15px 10px 6px;
  display: ${props => (props.hidden ? 'none' : 'block')};
`;

export const LinkContainer = styled.div`
  font-size: 20px;
  margin-right: 30px;
  padding: 5px 10px 5px 0;
  position: relative;

  a {
    color: #000;
    position: relative;
    transition: all 0.2s ease-out 0.2s;
  }

  span {
    position: absolute;
    top: 7px;
    right: calc(100% + 5px);
    height: calc(100% - 15px);
    width: 0;
    background-color: #000;
    transition: all 0.2s ease-out;
  }

  &:not(:last-child) {
    position: relative;

    &:after {
      content: '';
      background-color: ${props =>
        props.inline ? 'rgba(0, 0, 0, 0.4)' : 'transparent'};
      width: 1px;
      height: calc(100% - 15px);
      position: absolute;
      top: 7.5px;
      right: -12px;
      transform: rotateZ(25deg);
    }
  }

  &:hover {
    a {
      color: ${props => (props.inline ? '#fff' : 'inherit')};
    }

    span {
      width: ${props => (props.inline ? '100%' : 'inherit')};
      right: ${props => (props.inline ? 0 : 'inherit')};
    }
  }
`;

export const BackLink = styled(Link)`
  color: #000;
  position: relative;
  padding: 5px 10px;
  font-size: 18px;

  &:before {
    content: '/';
    color: rgba(0, 0, 0, 0.4);
    padding-right: 5px;
  }

  @media print {
    display: none;
  }
`;

export default Landing;
