const pad = (str, len, del = ' ') => {
  while (str.length < len) {
    str = del + str;
  }
  return str;
};

const months = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

export const formatDate = (date, format) => {
  const d = new Date(date);
  if (isNaN(d.valueOf())) return date;

  return format
    .replace(/YYYY/g, d.getFullYear())
    .replace(
      /YY/g,
      d
        .getFullYear()
        .toString()
        .slice(2),
    )
    .replace(/MMMM/g, months[d.getMonth()])
    .replace(/MMM/g, months[d.getMonth()].slice(0, 3))
    .replace(/MM/g, pad(d.getMonth() + 1, 2, '0'))
    .replace(/DD/g, pad(d.getDate(), 2, '0'));
};
